body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 70s linear;
          animation: App-logo-spin infinite 70s linear;
  height: 180px;
  border-radius: 15px;
  border: 2px solid black;
}

.App-header {
  background-color: #20c997;
  height: 280px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}

#topicHelp {
  font-weight: bold;
  font-style: italic;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.myTextInput {
  color: #777;
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.delete-btn {
  float: right;
  color: #e74944;
}

.searchBtn {
  background-color: #20c997;
  font-weight: bolder;
}

.mytitle {
  color: #111;
  font-weight: bolder;
}
.scrollable {
  overflow-y: auto;
  max-height: 5%;
  color: rgb(5, 84, 121);
}

.list-overflow-container {
  max-height: 545px;
  border: 1px #DDDDDD solid;
  border-radius: 4px;
  overflow-y: scroll;
  overflow-x: hidden;
}

.list-overflow-container ul {
  margin: 0;
}

.list-overflow-container li {
  border-radius: 0px;
  border-left: none;
  border-right: none;
}

.list-overflow-container li * {
  cursor: pointer;
}

.list-overflow-container li a {
  color: #2A878C;
}

.list-overflow-container li:first-child {
  border-top: none;
}

.list-overflow-container li:last-child {
  border-bottom: none;
}

